@import url('http://fonts.cdnfonts.com/css/chunkfive');
.container.nav {
  z-index: 999;
  position: fixed;
  top: 0;
  width: 100%;
  padding: 20px 25px;
  border-bottom: .8px solid black;
  background: rgb(17, 19, 18, .3);
  backdrop-filter: blur(3px);
  transform: translateY(-200px);
  transition: all .5s ease;
}
.container.nav.nav-active {
  transform: translateY(0);
}
.nav > ul {
  display: flex;
  justify-content: space-between;
}
.nav > ul li {
  color: var(--color-default);
  font-size: 15px;
  text-transform: capitalize;
  cursor: pointer;
}
.container.header figure > figcaption {
  padding: 0 25px;
}
.header > figure {
  position: relative;
}
.header > figure img {
  width: 100%;
  height: 100vh;
  object-fit: cover;
  filter: brightness(.6);
}
.header > figure figcaption {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}
.header > figure figcaption p {
  color: var(--color-default);
}
.header > figure figcaption p:nth-child(1) {
  font-size: 30px;
  text-transform: uppercase;
}
.header > figure figcaption p:nth-child(1) > span {
  display: flex;
  font-size: 35px;
  font-weight: 900;
}
.header > figure figcaption > ul {
  display: flex;
  gap: 10px;
  margin: 20px 0;
}
.header > figure figcaption > ul li button {
  color: var(--color-default);
  padding: 5px 12px;
  font-size: 15px;
  text-transform: capitalize;
  border: 1px solid var(--color-default);
  background: transparent;
  border-radius: 20px;
  cursor: pointer;
}
.header > figure figcaption p:nth-child(2) { 
  display: flex;
  gap: 5px;
}
.icons {
  z-index: 999;
  position: absolute;
  left: 25px;
  bottom: 50px;
}
.icons ul {
  display: flex;
  gap: 20px;
}
.icons ul li a {
  display: block;
  color: rgb(255, 255, 255, .5);
  font-size: 25px;
}

@media screen and (min-width: 700px) {
  .container.nav,
  .container.header figure > figcaption {
    padding-left: 35px;
  }
  .icons {
    left: 35px;
  }
  .nav > ul {
    gap: 50px;
    justify-content: flex-start;
  }
}

@media screen and (min-width: 1035px) {
  .container.nav,
  .container.header figure > figcaption {
    padding-left: 55px;
  }
  .icons {
    left: 55px;
  }
}

@media screen and (min-width: 1200px) {
  .container.nav,
  .container.header figure > figcaption {
    padding-left: 85px;
  }
  .icons {
    left: 85px;
  }
}
