.container.about {
  padding: 90px 25px 10px;
}
.container.resume  {
  padding: 25px;
}
.about div > h1,
.resume div > h1 {
  display: inline;
  color: var(--color-default);
  font-size: 20px;
  text-transform: uppercase;
  border-bottom: 2px solid var(--color-light);
}
.about div > p:nth-child(2) {
  color: var(--color-default);
  padding: 10px 0;
  font-size: 14px;
  font-family: monospace;
}
.about div > p:nth-child(3) {
  color: var(--color-default);
  padding: 50px 0;
  font-size: 15px;
  line-height: 25px;
}
.about div > p > span,
.about div > p > span a {
  color: var(--color-light);
}

.resume > div > ul {
  padding: 25px 0 55px;
}
.resume > div > ul li img {
  display: block;
}
.resume > div:nth-child(1) > ul li:nth-child(1) img {
  width: 25px;
}
.resume > div:nth-child(1) > ul li:nth-child(2) img {
  width: 25px;
}
.resume > div:nth-child(1) > ul li:nth-child(3) img {
  width: 25px;
}
.resume > div:nth-child(1) > ul li:nth-child(4) img {
  width: 30px;
}
.resume > div:nth-child(1) > ul li:nth-child(5) img {
  width: 30px;
}

.resume > div > ul li {
  display: flex;
  align-items: center;
  color: var(--color-default);
  padding: 10px 0;
}
.resume > div > ul li:nth-child(1) {
  gap: 20px;
}
.resume > div > ul li:nth-child(2) {
  gap: 20px;
}
.resume > div > ul li:nth-child(3) {
  gap: 20px;
}
.resume > div > ul li:nth-child(4) {
  gap: 15px;
}
.resume > div > ul li:nth-child(5) {
  gap: 15px;
}
.resume > div > ul li > p {
  width: 130px;
}
.resume > div > ul li:nth-child(4) > p,
.resume > div > ul li:nth-child(5) > p {
  width: 140px;
}
.resume > div > ul li > span {
  position: relative;
  display: inline-block;
  width: 100%;
  height: 2px;
  background: var(--color-default);
  border-radius: 20px;
}
.resume > div > ul li > span::before {
  position: absolute;
  top: -10px;
  display: block;
  content: '';
  width: 20px;
  height: 20px;
  border: 1px solid var(--color-default);
  background: var(--background-default);
  border-radius: 50%;
}
.resume > div > ul li > span::after {
  position: absolute;
  top: -4px;
  left: 6px;
  display: block;
  content: '';
  width: 10px;
  height: 10px;
  background: var(--color-default);
  border-radius: 50%;
}
.resume > div:nth-child(1) > ul li:nth-child(1) > span::before,
.resume > div:nth-child(1) > ul li:nth-child(2) > span::before {
  right: 0;
}
.resume > div:nth-child(1) > ul li:nth-child(1) > span::after,
.resume > div:nth-child(1) > ul li:nth-child(2) > span::after {
  left: calc(100% - 16px);
}

.resume > div:nth-child(1) > ul li:nth-child(3) > span::before {
  right: 30px;
}
.resume > div:nth-child(1) > ul li:nth-child(3) > span::after {
  left: calc(100% - 46px);
}

.resume > div:nth-child(1) > ul li:nth-child(4) > span::before {
  right: 40px;
}
.resume > div:nth-child(1) > ul li:nth-child(4) > span::after {
  left: calc(100% - 56px);
}

.resume > div:nth-child(1) > ul li:nth-child(5) > span::before {
  right: 30px;
}
.resume > div:nth-child(1) > ul li:nth-child(5) > span::after {
  left: calc(100% - 46px);
}
.resume > div:nth-child(2) > ul li img {
  width: 35px;
}

.resume > div:nth-child(2) > ul li:nth-child(1) > span::before {
  right: 70px;
}
.resume > div:nth-child(2) > ul li:nth-child(1) > span::after {
  left: calc(100% - 86px);
}
.resume > div:nth-child(2) > ul li:nth-child(2) > span::before {
  right: 100px;
}
.resume > div:nth-child(2) > ul li:nth-child(2) > span::after {
  left: calc(100% - 116px);
}
.resume > div:nth-child(2) > ul li:nth-child(3) > span::before {
  right: 70px;
}
.resume > div:nth-child(2) > ul li:nth-child(3) > span::after {
  left: calc(100% - 86px);
}

@media screen and (min-width: 700px) {
  .container.about,
  .container.resume {
    padding-left: 35px;
  }
  .container.about > div {
    max-width: 500px;
  }
  .resume {
    display: flex;
    gap: 10%;
    justify-content: space-between;
  }
  .resume > div {
    width: 100%
  }
}

@media screen and (min-width: 1035px) {
  .container.about,
  .container.resume {
    padding-left: 55px;
  }
}

@media screen and (min-width: 1200px) {
  .container.about,
  .container.resume {
    padding-left: 85px;
  }
}
