.container.buttonArrow {
  z-index: 999;
  position: fixed;
  right: 25px;
  bottom: 25px;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  backdrop-filter: blur(3px);
  background: rgb(255, 255, 255, .3);
  opacity: 0;
  transform: scale(0);
  transition: all .3s ease;
}
.buttonArrow {
  color: var(--background-default);
  font-size: 25px;
  line-height: 55px;
  text-align: center;
}
.container.buttonArrow.button-active {
  opacity: 1;
  transform: scale(1);
}
