.container.footer {
  background: #252A29;
  padding: 65px 25px;
 /*  background: #3E4542; */
}
.footer ul h1 {
  color: var(--color-default);
  font-size: 26px;
  text-transform: capitalize;
}
.footer ul:nth-child(1) h2 {
  color: var(--color-light);
  font-size: 22px;
  margin: 10px 0;
  text-transform: capitalize;
}
.footer ul li  {
  color: var(--color-default);
  font-size: 15px;
  line-height: 23px;
}

.footer ul:nth-child(2) h2,
.footer ul:nth-child(3) h2 {
  color: var(--color-default);
  margin: 20px 0 10px;
  font-size: 22px;
  text-transform: capitalize;
}

.footer > span {
  display: block;
  width: 100%;
  height: 1px;
  margin: 30px 0;
  background: var(--color-default);
}

#icons ul {
  display: flex;
  gap: 20px;
  justify-content: center;
}
#icons ul li {
  font-size: 27px;
}
#icons ul li a {
  color: var(--color-default);
}
#icons > p {
  color: var(--color-default);
  margin: 14px 0;
  text-align: center;
  font-size: 15px;
}
#icons p span:nth-child(1) {
  color: rgb(255, 0, 0);
  font-size: 20px;
}
#icons p span:nth-child(2) {
  font-weight: bold;
}
#icons p span:nth-child(3) {
  color: var(--color-light);
  font-size: 16px;
  font-weight: bold;
}



@media screen and (min-width: 700px) {
  .footer div:nth-child(1) {
    display: flex;
    align-items: flex-start;
    justify-content: space-around;
  }
}
